:root{
    --active: rgb(20, 45, 66);

    --main: #7286D3;
    --second: #8EA7E9;
    
    --text: #000;
    
    --third: #0a101d;
}

.light-theme{
    --active: rgb(20, 45, 66);

    --main: #7286D3;
    --second: #8EA7E9;
    
    --text: #000;


    --mainBackground: #abb6d7;
}

.dark-theme{
    --active: #28e;

    --main: #151922;
    
    --text: #fff;

    --mainBackground: #151922;

}

:root{scroll-behavior: smooth;}

.mainHeader{
    color: var(--text);
    min-height: 100vh;
}

.pictureFrame{
    display: inline-block; 
    margin: auto;
}

.pictureFrame > img{
    height: 100%;
    width: 100%;
}

@media screen and (max-width: 767px){  /* movil */
    :root{scroll-padding-top: 55px;}
    .navSpacer{
        grid-column: 1/5;
        height: 90px;
    }
    
    .mainHeader{
        display: grid;
        grid-template-columns: 75px 75px 1fr 1fr;
        padding-bottom: 20px;
    }
    
    
    .myInfo{
        margin-bottom: 5px;
    }  
    
    .pictureFrame{
        
        border-radius: 50%;
        border: solid 4px var(--active);
        
        height: 120px;
        width: 120px;
        
        margin-top: 50px;
        overflow: hidden;
    }
    
    .photoContainer{
        display: flex;
        grid-column: 1/3;
    }
     
    .myInfoContainer{
        margin-top: 60px;
        grid-column: 3/5;
    }
    .myInfoContainer .myName{
        font-size: 1.8em;
        font-weight: 600;
        margin-bottom: 10px;
    }
    .myInfoContainer .myEmail{
        font-size: 0.9em;
    }
    
    .speechContainer{
        grid-column: 1/5;
        text-align: justify;
        padding: 20px;
    }
    
}

@media screen and (min-width: 768px){ /* Descktop */
    :root{scroll-padding-top: 38px;}
    .navSpacer{
        height: 38px;
        grid-column: 1/-1;
        margin-bottom: 100px;
    }

    .mainHeader{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }


    .photoContainer{
        text-align: end;
        padding-right: 100px;
    }
    
    .pictureFrame{  
        height: 200px;
        width: 150px;
        margin: auto;
        
        background: var(--active);
    }
    .pictureFrame img{
        -webkit-transform: translate(25px, -25px);
                transform: translate(25px, -25px);
        border: solid 2px var(--mainDark);
        box-shadow: -10px 15px 10px #0008;
    }

    .myInfoContainer{
        display: flex;
        flex-direction: column;
        grid-gap: 10px;
        gap: 10px;
        justify-content: center;
    }

    .myInfoContainer .myName{
        font-size: 2em;
        font-weight: 600;
        margin-bottom: 10px;
    }

    .speechContainer{
        text-align: end;
        width: 60%;
        margin: auto;
        margin-top: 40px;
        margin-bottom: 40px;
        grid-column: 1/-1;
    }
    .mySpeech{
        text-align: left;
    }
}
.mainNav{
    display: grid;
    
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    background: var(--third);
    z-index: 1000000;

    box-shadow: 0px 0px 20px var(--third);

    transition: top 0.3s ease;
}

.sp1 i{
    color: #fff;
}


.mainMenu ul{
    display: grid;
}

.mainMenu li{
    list-style: none;
}
.mainMenu a{
    text-decoration: none;
    color: #fff;
    text-transform: capitalize;
}

.downloadCv{ 
    color: var(--active);
    padding: 5px 10px;

    margin-top: 20px;

    font-size: 1.2em;
    font-weight: 600;
    text-transform: capitalize;
    
    background: none;
    border: solid 4px var(--active);
    border-radius: 0px;
    transition: 
        color 0.6s ease, 
        border 0.6s ease
    ;
}
.downloadCv:hover{
    color: #fff;
    border-color: #fff;
    cursor: pointer;
}

.mainMenu li{
    overflow: hidden;
}
.mainMenu li a:hover{
    color: #000;
    border-bottom: solid 2px #000;
}

.mainMenu li a::after{
    content: '';
    display: inline-block;
    position: absolute;
    top: 0%;
    left: 0%;

    width: 100%;
    height: 100%;


    background: #ccc;
    z-index: -1;
    -webkit-transform: translateX(-110%);
            transform: translateX(-110%);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.mainMenu li a:hover::after{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.confi-options{
    display: flex;
    flex-flow: row;
    grid-gap: 10px;
    gap: 10px;
}



@media screen and ( max-width: 767px){ /* Movil */
    
    .mainNav{
        display: grid;
        grid-template-columns: auto 1fr auto;
        text-align: end;
    }

    #menuBtn{
        margin: 10px;
        font-size: 1.1em;
        color: #222f3e;
        padding: 8px;
        background: none;
        border: solid 2px #fff;
        border-radius: 8px;
        
    }
    
    #menuBtn path{
        fill: #fff;
    }


    .sp1{
        padding-left: 20px;
        display: flex;
        margin: auto;
        font-size: 1.6em;
    }
    
    .mainMenu{
        grid-column: 1/4;
        overflow: hidden;
        height: 100vh;
        transition: max-height 0.6s ease, margin-top 0.6s ease ;
    }
    .mainNavOpen{
        margin-top: 60px; 
        max-height: 1000px;
    }
    .mainNavClose{
        max-height: 0;
        margin-top: 0px; 
    }
    
    .mainMenu li a{
        position: relative;
        display: inline-block;
        width: 100%;
        
        padding: 20px;

        text-transform: uppercase;
        font-weight: 500;
        font-size: 1.5em;
        text-align: center;

        background: #222f3e;
        
        border: solid 2px transparent;
        border-bottom-color:#fff;
        z-index: 2;
        transition: color 0.6s ease;
    }

    .cvContainer{
        text-align: center;
        padding: 20px;
    }
    .mainMenu .downloadCv{
        font-size: 1.5em;
        font-weight: 600;
        padding: 10px 50px;
        border: solid 4px #28e;
        color: #28e;
        border-radius: 0px;
        margin-top: 10px;
    }

    .confi-options{
        height: 45px;
        justify-content: center;
        margin-bottom: 20px;
    }

}

@media screen and (min-width: 768px){ /* Descktop*/
    .mainNav{
        display: flex;
        justify-content: space-between;
    }
    .sp1{
        padding: 10px;
    }
    #menuBtn{
        display: none;
    }
    
    .mainMenu {
        display: flex;
        justify-items: center;
    }

    .mainMenu li{
        display: flex;
        position: relative;
    }
    .mainMenu li a{
        margin: auto;
        display: inline-block;
        height: 100%;
        padding: 0 15px;
        padding-top: 10px;

        border-bottom: solid 2px #fff;
    }
    
    .mainNav .downloadCv{
        display: none;
    }    

}
