.formArea{
    padding-bottom: 6em;
}
.inputGroup{
    position: relative;
    height: 50px;
    margin-bottom: 20px;
    overflow: hidden;
}

.textAreaGroup{
    position: relative;
    width: 100%;
    height: 150px;
