#emailMessage{
    background: none;
    color: #fff;
    width: 100%;
    height: 100%;
    font-size: 1.2em;
    border: none;
    resize: none;
    position: relative;
    z-index: 2;
    background: var(--third);
    padding: 5px;
    outline: none;
}


.bg-border{
    position: absolute;
    top: 0;
    left: 0;
    
    height: 100%;
    width: 100%;
    
    background: linear-gradient(240deg, transparent 20%, #fff);
    z-index: 1;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    border-bottom: solid 2px #fff;
    /* transform: translateX(-25%); */
}
#emailMessage:valid + .bg-border{
    background: linear-gradient(240deg, transparent 20%, var(--active));
    /* transform: translateX(0%); */
    border-bottom: solid 2px var(--active);
}

.emailInput{
    height: 100%;
    width: 100%;
    padding-left: 5px;
    padding-top: 15px;

    color: var(--text);
    font-size: 1.2em;
    
    border: none;
    outline: none;
    background: none;
}

.inputLabel{
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    
    border-bottom:  2px solid #fff;
    pointer-events: none;
}

.lavelContent{
    position: absolute;
    bottom: 4px;
    left: 0;
    font-size: 1.2em;
    color: #fff;
    transition: 
        font-size 0.6s ease,  
        color 0.6s ease, 
        -webkit-transform 0.6s ease;
    transition: 
        transform 0.6s ease, 
        font-size 0.6s ease,  
        color 0.6s ease ;
    transition: 
        transform 0.6s ease, 
        font-size 0.6s ease,  
        color 0.6s ease, 
        -webkit-transform 0.6s ease;
}

.inputLabel::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    
    height: 100%;
    width: 100%;

    border-bottom:  2px solid var(--active);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}

.emailInput:focus + .inputLabel .lavelContent,
.emailInput:valid + .inputLabel .lavelContent{
    color: var(--active);
    -webkit-transform: translateY(-150%);
            transform: translateY(-150%);
    font-size: smaller;
}
.emailInput:focus + .inputLabel::after,
.emailInput:valid + .inputLabel::after{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.formBtnArea{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    gap: 10px;
}
.formBtnArea .emailBtn{
    padding: 8px;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
}

.formArea .emailSubmit{
    background: var(--active);
    color: #fff;
    border: none;
    transition: all 0.6s ease;
}
.formArea .emailSubmit:hover{
    background: var(--active);
}
.formArea .emailSubmit:active{
    font-size: 0.8em;
}

.formArea .emailReset{
    background: #ccc;
    border: none;
}

.hiddenCheck{
    display: none;
}
.hiddenCheck + label{
    color: var(--text);
    transition: color 0.6s ease, -webkit-transform 0.6s ease;
    transition: transform 0.6s ease, color 0.6s ease;
    transition: transform 0.6s ease, color 0.6s ease, -webkit-transform 0.6s ease;
}
.hiddenCheck:checked + label{
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
    color: var(--active) !important;
}

.email-ready{
    transition: color 0.6s ease, -webkit-transform 0.6s ease;
    transition: transform 0.6s ease, color 0.6s ease;
    transition: transform 0.6s ease, color 0.6s ease, -webkit-transform 0.6s ease;

    -webkit-transform: rotate(-20deg);

            transform: rotate(-20deg);
    color: var(--active);
}


