.viewer{
    display: flex;
    flex-direction: column;

    margin-bottom: 40px;
    
    height: 150px;
    width: 250px;


    color: #fff;
    background: #0008;
    border-radius: 25px;
    cursor: pointer;
}

.viewer picture{
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    width: 100%;
    border-radius: 25px;
}

.viewer img{
    margin: auto;
    height: 100%;
    width: 100%;
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}

.viewer:hover img{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.viewer a{
    display: inline-block;
    position: absolute;
    bottom: 0;
    
    left: 0;
    width: 80%;
    
    padding: 4px 20px;
    
    text-align: right;
    color: #fff;

    background: #28e;
    border-radius: 0px 15px 0px 0px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}
.viewer a:hover{
    color: #000;
}
.viewer:hover a{
    -webkit-transform: translateX(0%);
            transform: translateX(0%);

}

.viewer h4{
    padding: 4px;
    display: inline-block;
    text-align: center;
    margin-top: 2px;
    transition: color 0.6s ease;
}
.viewer:hover h4{
    color: rgb(75, 164, 253);
}

