/* 
  Móvil: 360 x 640
  Móvil: 375 x 667
  Móvil: 360 x 720
  iPhone X: 375 x 812
  Pixel 2: 411 x 731
  Tableta: 768 x 1024
  Un ordenador portátil: 1366 x 768
  Un ordenador portátil o desktop de alta resolución: 1920 x 1080

  -------------------------------------------------------------------

  Desktop -> min: 1024

  Tablet -> min: 768 and max: 1023

  Movil -> max: 767
  
  -------------------------------------------------------------------

  sharp glass -> #4b749f + #243748

  Miracle -> #392d69 + #b57bee
  
*/


*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  scroll-behavior: smooth;
}

.icon-left{
  margin-right: 10px;
}
.icon-right{
  margin-left: 10px;
}


.App{
  background: var(--mainBackground);
  transition: background 0.6s ease;
}
